export class CallbackRequest {

    public name : any;
    public email : any;
    public phone : any;
    public comment : any;

    constructor(
        name :string,
        email : string,
        phone : string,
        comment : string){
            this.name = {'ru-UA' : !name ? '< >' : name };
            this.email = {'ru-UA' : !email ? '< >' : email };
            this.phone = {'ru-UA' : !phone ? '< >' : phone };
            this.comment = {'ru-UA' : !comment ? '< >' : comment };
    }
}